import React, { useEffect, useState } from 'react'
import {Button, TextField, Modal, Box,} from "@mui/material";
import AddIcon from '@mui/icons-material/Add';
import img1 from '../assets/googlesheets.png';
import Papa from "papaparse";


const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  transform: 'translate(-50%, -50%)',
  width: 480,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

function GoogleSpreadsheet(props) {

  const [data, setData] = useState({});
  let count = 0;

  const getWords = (url) => {
    Papa.parse(url, {  // https://docs.google.com/spreadsheets/d/e/2PACX-1vTD1doNL8jJSjKip4pqYE7sEpl_SUgYgaOFCw5gNrBaNmONzyoWQjHwJBIP0revFRuocq2gZ1om78ZR/pub?gid=0&single=true&output=csv
      download: true,
      header: true,
    
      complete: (results) => {
        console.log(results)
        setData(results.data);
        props.addWords(results.data);
      },
    });
  }  

  const words = Array.from(data);

  const [open, setOpen] = React.useState(false);
  const [url, setUrl] = React.useState('');

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Button onClick={handleOpen} startIcon={<AddIcon />}>Add From Google Sheets</Button>
      <Modal open={open} onClose={handleClose} disableScrollLock={true}>
        <Box sx={style}>
          <h2>Google dökümanlardan ekleme</h2>
          <p>Bir google dökümanlardaki kelime listenizi kullanarak kendi kelime listenizi üretebilirsiniz. Ürettiğiniz listenin başlıkları aşağıda gösterildiği (english, turkish, example) şekilde olmalıdır.</p>
          <img src={img1} width="480"></img>

          <h4>Dosyayı csv olarak paylaşın</h4>
          <p>Dosya, Paylaş ve ardından Web'de Paylaş seçeneklerini tıklayın, ilgili sheet'i seçin csv olarak elde ettiğiniz linki aşağıda giriniz:</p>
          {/* <img src={gif} width="480"></img> */}
          <TextField onChange={(e)=>{setUrl(e.target.value)}} value={url} style = {{width:"500px", marginBottom: '20px'}} />
          <Button variant="contained" startIcon={<AddIcon />} onClick={()=>{getWords(url); handleClose()}}>Add</Button>
         
        </Box>
      </Modal>
    </div>
  )
}

export default GoogleSpreadsheet