import React from 'react'
import {Button, TextField, Modal, Box,} from "@mui/material";
import UploadFileIcon from '@mui/icons-material/UploadFile'
import AddIcon from '@mui/icons-material/Add';
import img1 from '../assets/googlesheets.png';
import gif from '../assets/howto.gif';


const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  display: 'flex',
  flexDirection: 'column',
  justifyContent: 'center',
  alignItems: 'center',
  transform: 'translate(-50%, -50%)',
  width: 480,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};

function CardUpload(props) {
  const [open, setOpen] = React.useState(false);
  const [word, setWord] = React.useState({english: '', turkish: '', example: ''});

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <div>
      <Button onClick={handleOpen} startIcon={<AddIcon />}>Upload List</Button>
      <Modal open={open} onClose={handleClose} disableScrollLock={true}>
        <Box sx={style}>
          <h2>Google dökümanlardan ekleme</h2>
          <p>Bir google dökümanlar kullanarak kendi kelime listenizi üretebilirsiniz. Ürettiğiniz listenin başlıkları aşağıda gösterildiği (english, turkish, example) şekilde olmalıdır.</p>
          <img src={img1} width="480"></img>

          <h4>Eklenti Yükleme</h4>
          <p>Üst menülerden Extensions (Eklentiler) sekmesini kullanarak "Export sheet data" eklentisini yükleyin. Bu eklenti sayesinde tüm dökümanı .json uzantısı ile indirebileceksiniz.</p>
          {/* <img src={gif} width="480"></img> */}
          
          <input style={{display: "none"}} accept =".json"  // specify the file type that you wanted to accept
            id="choose-file"
            type="file"
            onChange={(e) => props.showFile(e)}
          />
          <label htmlFor="choose-file">
            <UploadFileIcon /> Upload a json file
          </label>
        </Box>
      </Modal>
    </div>
  )
}

export default CardUpload